@import url(https://fonts.googleapis.com/css?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css?family=Slabo+27px&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,body
{
  background-color: black;
  color:white;
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
}
header{
    height:5em;
    color:white;
    margin:0px;
    padding:0px;
}

ul{
    display:flex;
    justify-content: space-between;
    padding:0px;
}

li{
    font-size:1.3em;
    margin:auto;
    list-style: none;
    padding:0.25em;
    padding-left:0.5em;
    padding-right:0.5em;
    text-transform: capitalize;
    letter-spacing: 1px;
    cursor: pointer;
    height:100%;
}

.nav-li:hover{
    color:#876f4b;
}

nav{
    margin:auto;
    display:flex;
    justify-content: space-between;
}

#profile-pic{
    border-radius:50%;
    padding:0.25em;
    margin:0.25em;
    padding-left:0.5em;
    height:4.2em;
    width:4.2em;
}

#profile-name{
    padding-left:5px;
    text-transform: uppercase;
    color:white;
    margin:auto;
    font-family: 'Playfair Display', serif;
    font-size:2em;
    letter-spacing: 3px;
}
.nav-button{
    background:transparent;
    border:none;
    outline:none;
    position:absolute;
    display:none;
    top:1.5em;
    right:1.5em;
    width:2.5em;
    height:3em;
}

.popup-button{
    width:32px;
    height:32px;
}


@media only screen and (max-width: 600px){
    .nav-ul{
        display:none;
    }
    .nav-button{
        display:block;
    }

    #profile-pic{
        display:none;
    }
    #profile-name{
        padding:0.5em;
    }
}

.nav-bar{
    font-family: 'Slabo 27px', serif;
}


#profile-link{
    display:flex;
    flex-grow: initial;
    text-decoration: none;
}

.mobile-nav-ul{
    z-index:1;
    display:flex;
    position:absolute;
    flex-direction: column;
    top:4em;
    text-align:center;
    width:100%;
    height:30%;
    justify-content: flex-start;
    background:rgba(0,0,0,0.9);
    -webkit-animation: show 0.5s linear;
            animation: show 0.5s linear;
}

@-webkit-keyframes show{
    0%{height:0px;opacity: 0;}
    50%{height:15%;opacity:0.4;}
    100%{height:30%;opacity:1;}
}

@keyframes show{
    0%{height:0px;opacity: 0;}
    50%{height:15%;opacity:0.4;}
    100%{height:30%;opacity:1;}
}

.mobile-nav-li{
    width:100%;
    padding-top:1em;
    padding-bottom:1em;
    text-align: center;
    text-transform: uppercase;
    font-weight:bold;
    font-size:1.5em;
}


.banner{
    color:white;
    text-align: center;
    background: -webkit-linear-gradient(315deg, #876f4b, black);
    background: linear-gradient(135deg, #876f4b, black);
    font-family: 'Playfair Display', serif;
    padding:0.2em;
    padding-bottom: 2em;
}

.banner_title {
    text-transform: uppercase;
    font-size: 3em;
    font-weight: lighter;
    margin-bottom: 0.4em;
    padding-left:0.2em;
    padding-right:0.2em;
}

.banner_subtitle {
    font-size: 2em;
    font-family: 'Slabo 27px', serif;
}

#banner_img{
    object-fit: scale-down;
    width: 60%;
    height: auto;
    margin: 3em;
    max-width: 940px;
    max-height: 450px;
    margin-top:5em;
}



.project{
    display:flex;
    flex-wrap: wrap;
    /*border-top: 2px solid #876f4b;*/
    border: 3px solid #876f4b;
    border-radius: 30px;
    margin:1em;
    min-width: 280px;

}

.project_tech>ul{
    flex-wrap:wrap;
}

.project > div{
    flex:1 1;
    padding: 10px;
}

.project_img{
    align-self: center;
    object-fit: scale-down;
    width: 60%;
    height: 60%;
    max-width: 400px;
    max-height: 600px;
    min-width: 200px;
    min-height:300px;
}

.project_img_container{
    background: -webkit-linear-gradient(315deg, #876f4b, black);
    background: linear-gradient(135deg, #876f4b, black);
    display:flex;
    justify-content: space-around;
    border-radius: 25px;
}

.project_desc_container{
    display:flex;
    width:100%;
    flex-direction:column;
    justify-content: center;
    text-align: center;
    font-family: 'Playfair Display', serif;
}

.project_title{
    font-size:2em;
    margin:0.5em;
}

.project_date{
    font-size:1.6em;
}

.project_description{
    font-size:1em;
}

h2{
    text-transform: uppercase;
}

@media only screen and (min-width:400px) {
    .project:nth-child(even) {
        flex-direction: row-reverse;
        min-height: 500px;
    }
}





.projects_section{
    color:white;
}

.container_title{
    text-align:center;
    font-size:2.8em;
    font-family: 'Playfair Display', serif;
    text-transform:uppercase;
    letter-spacing: 5px;
    font-weight:lighter;
    position:relative;
    padding:1.25em;
    margin:auto;
    overflow: hidden;
    background: -webkit-gradient(linear, left top, right top, from(grey), color-stop(#fff), to(black));
    background: -webkit-linear-gradient(left, grey, #fff, black);
    background: linear-gradient(90deg, grey, #fff, black);
    background-repeat: no-repeat;
    background-size: 90%;
    -webkit-animation: animate 3s linear infinite;
            animation: animate 3s linear infinite;
    -webkit-background-clip: text;
    -webkit-text-fill-color: rgba(255, 255, 255, 0);
}

@-webkit-keyframes animate{
    0%{
        background-position:-200%;
    }
    100%{
        backgorund-position:200%;
    }
}

@keyframes animate{
    0%{
        background-position:-200%;
    }
    100%{
        backgorund-position:200%;
    }
}

.form-input, .textarea-div{
    background-color: black;
    border: 1px solid white;
    padding: 0.6em;
    width:100%;
    font-size:1.2em;
    color:white;
    outline:none;
    min-width:60%;
    box-sizing: border-box;
    -webkit-box-sizing:border-box;
    -moz-box-sizing: border-box;
}

.textarea-div > textarea{
    margin:0px;
    padding:0px;
    max-width:100%;
    max-height:100%;
    border:none;
    resize:none;
    width:100%;
}



::-webkit-input-placeholder {
    color: lightgrey;
    opacity: 1; /* Firefox */
}



::-moz-placeholder {
    color: lightgrey;
    opacity: 1; /* Firefox */
}



:-ms-input-placeholder {
    color: lightgrey;
    opacity: 1; /* Firefox */
}



::-ms-input-placeholder {
    color: lightgrey;
    opacity: 1; /* Firefox */
}



::placeholder {
    color: lightgrey;
    opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: lightgrey;
}

::-ms-input-placeholder { /* Microsoft Edge */
    color: lightgrey;
}

.contact-form{
    display:flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap:wrap;
    background-color: #181818;
    padding-top:4em;
    padding-bottom:6em;
}

.form-left{
    text-align: center;
    padding:10px;
    margin:auto;
}

.form-left h2{
    font-stretch: normal;
    font-size:50px;
    letter-spacing: 2px;
    font-family: 'Slabo 27px', serif;
    font-weight: bold;
}

.form-left p{
    font-weight:normal;
    font-stretch:normal;
    font-size:15px;
}



.name-input{grid-area:name;}
.address-input{grid-area:address;}
.email-input{grid-area:email;}
.phone-input{grid-area:phone;}
.subject-input{grid-area:subject;}
.message-input{grid-area:message;}
.submit-input{grid-area:submit;}

.form-right{
    display:grid;
    grid-template-areas:
    'name name''address address''email phone''subject subject''message message''. submit';
    grid-row-gap:0.8em;
    margin:auto;
    padding:10px;
}

@-webkit-keyframes showmessage{
    0%{opacity: 0;}
    50%{opacity:0.5;}
    100%{opacity:1;}
}

@keyframes showmessage{
    0%{opacity: 0;}
    50%{opacity:0.5;}
    100%{opacity:1;}
}


.response-message{
    -webkit-animation: showmessage 1s linear;
            animation: showmessage 1s linear;
}


#submit{
    width:100%;
    background-color:#181818;
    margin-left:1.5em;
    color:white;
    border: solid white 1px;
    cursor: pointer;
    text-transform: capitalize;
    letter-spacing: 2px;
    font-size:15px;
    height:3em;
    text-transform: uppercase;
    box-sizing: border-box;
    -webkit-box-sizing:border-box;
    -moz-box-sizing: border-box;
}

.phone-input{
    padding-left:0.5em;
}

.submit-input{
    padding-right:1.4em;
}

#submit:hover{
    background-color: #876f4b;
    box-shadow: #181818;
    border:1px solid black;
}
.footer-icon{
    width:32px;
    height:32px;
}

.footer-title{
    text-align:center;
    font-family: 'Slabo 27px', serif;
    letter-spacing: 3px;
    font-weight:lighter;
    font-size:1.6em;
}

.social-div{
    display:flex;
    flex-direction:column;
    align-items: center;
    text-transform: capitalize;
}

.social-list{
    display:flex;
    flex-wrap: nowrap;
    justify-content: space-around;
}

@media only screen and (max-width:600px){
    .social-list{
        flex-wrap: wrap;
        margin: 5px;
    }
}


.quote-banner{
    background-color: #876f4b;
    display:flex;
    justify-content: center;
    flex-wrap:wrap;
    margin-top:3em;
    margin-bottom:2em;
}

.quote-img{
    align-self: center;
    object-fit: scale-down;
    width:280px;
}

.quotebanner-left{
    font-family: 'Playfair Display', serif;
}

.quotebanner-left,.quotebanner-right{
    margin-top:auto;
    margin-bottom:auto;
    padding:1em;
}

@media only screen and (min-width:600px){
    .quotebanner-left,.quotebanner-right{
        padding:3em;
    }

    .quote-img{
        min-width:500px;
    }
}

.quotebanner-left>p{
    font-weight: lighter;
}

.quotebanner-left>h3{
    letter-spacing: 1px;
}






